#root * {
  font-family: 'Kuro', sans-serif;
  font-size: 1.2rem;
  
}               
*{
  margin: 0;
  padding: 0;
 
}
#root button{
  width: 20%;
}
.main{
  width: 40%;
  margin: 10px auto;
  text-align: center;
  border: 1px solid black;
}
.main label{
  display: inline-block;
  width: 30%;
  text-align: left;
  
}
.main input{
  width: 30%;
  margin: 5px;
}
.main input[type=submit] , button{
  width: 20%;
  margin: 10px 0 ;
  padding: 5px ;
  margin:  5px;
}
nav a{
  margin: 2px 3px;
  text-decoration: none;
  display: inline-block;
  padding:0 10px;
  color: black;
}
nav span{
  display: inline;
  float: left;
}
input{
  padding: 5px;
}
.DivMain span{
  padding: 10px;
  display: inline-block;
  width: 60%;
  text-align: left;
  margin: 4px auto;
  border: 1px solid black;
  
}
.DivModif span{
  display: inline-block;
  width: 40%;
  background-color: antiquewhite;  
}
.DivModif input{
  display: inline-block;
  width: 40%;
}

#modifier{
  background-color: rgb(255, 255, 38);
}
#supprimer{
  background-color: rgb(255, 56, 56);
}
#Ajouter{
  background-color: rgb(18, 173, 18);
}
#Rechercher{
  background-color: rgb(123, 123, 236);
}
#lister{
  background-color: rgb(156, 104, 104);
}
